import React, { Component, Suspense } from 'react';
import { toast } from "react-toastify";
import { Accordion, ToggleButtonGroup, ToggleButton } from 'react-bootstrap';
import PerfectScrollbar from 'react-perfect-scrollbar'
import ModernWaterfallTemplate from './Components/ModernWaterfallTemplate';
import ModernSlideshowTemplate from './Components/ModernSlideshowTemplate';
import AtmosphereTemplate from './Components/AtmosphereTemplate';

import SquarePhoto from '../ThemeTemplates/SquarePhoto';
import ModernCard from '../ThemeTemplates/ModernCard';
import HorizontalColumns from '../ThemeTemplates/HorizontalColumns';
import ModernSlideShow from '../ThemeTemplates/ModernSlideShow';
import Atmosphere from '../ThemeTemplates/Atmosphere';
import GalleryPhoto from '../ThemeTemplates/GalleryPhoto';
import ModernWaterfall from '../ThemeTemplates/ModernWaterfall';
import FontHead from './FontHead';

import { getThemesList, createTemplate } from '../../../../Services/Actions/templateAction'

const ModernCardTemplate = React.lazy(() => import('./Components/ModernCardTemplate'));
const SquarePhotoTemplate = React.lazy(() => import('./Components/SquarePhotoTemplate'));
const HorizontalColumnTemplate = React.lazy(() => import('./Components/HorizontalColumnTemplate'));
const GalleryPhotoTemplate = React.lazy(() => import('./Components/GalleryPhotoTemplate'));
const ColorPicker = React.lazy(() => import('../../CommonComponents/ColorPicker'));

const ThemesPreview = ({ themeName, templateFormData }) => {
    switch (themeName) {
        case 20:
            return <ModernCard {...templateFormData} />;
        case 4:
            return <SquarePhoto {...templateFormData} />;
        case 47:
            return <HorizontalColumns  {...templateFormData} />;
        case 21:
            return <ModernSlideShow {...templateFormData} />;
        case 44:
            return <Atmosphere {...templateFormData} />;
        case 50:
            return <GalleryPhoto {...templateFormData} />;
        case 58:
            return <ModernWaterfall {...templateFormData} />;
        default:
            return <ModernCard {...templateFormData} />;
    }

}

