class TemplateAside extends Component {
    constructor(props) {
        super(props)
        this.state = {
            templateName: props.defaultTheme && props.defaultTheme.themeDefaultOption ? props.defaultTheme.themeDefaultOption.name : "",
            selectedTheme: props.defaultTheme ? { name: props.defaultTheme.name, themeId: props.defaultTheme.themeId } : {},
            selectedThemeData: props.defaultTheme ? props.defaultTheme : null,
            templateFormData: null,
            currentTheme: props.activeTheme ? props.activeTheme : null,
            isRequesting: false,
            tempError: false
        }
    }

    onChooseTheme = (theme, selectedThemeData) => event => {
        this.setState({ selectedTheme: theme, selectedThemeData: selectedThemeData }, () => this.props.changeThemeFunCallBack(selectedThemeData.themeId))


    }
    onTextTemplateName = event => {
        this.setState({ templateName: event.target.value, tempError: false })
    }
    tempFormData = (data) => {
        this.setState({ templateFormData: data })
    }
    onClickSaveChanges = event => {
        const { templateFormData, templateName, selectedThemeData } = this.state
        const { templateId } = this.props
        if (templateFormData && Object.keys(templateFormData).length > 0 && String(templateName).length > 0) {
            var self = this
            const data = {
                "action": 2,
                "id": templateId,
                "name": templateName,
                "theme": selectedThemeData.themeId,
                "cssStatus": templateFormData.customCss.cssStatus ? 1 : 0,
                "css": templateFormData.customCss.css,
                "backgroundColor": templateFormData.background.color,
                "transparent": templateFormData.background.transparent ? 1 : 0,
                "backgroundImage": templateFormData.background.image ? templateFormData.background.image : "",
                "backgroundImageId": 0,
                "link_font": templateFormData.cardStyle.fontFamily ? templateFormData.cardStyle.fontFamily.family : "",
                "css_font": templateFormData.cardStyle.fontFamily ? templateFormData.cardStyle.fontFamily.family : "",
                "font_varient": templateFormData.cardStyle.fontVarient && Object.keys(templateFormData.cardStyle.fontVarient).length > 0 ? templateFormData.cardStyle.fontVarient.value : "",
                "fontColor": templateFormData.cardStyle.textColor,
                "cardColor": templateFormData.cardStyle.cardColor,
                "postText": 0,
                "postAuthor": templateFormData.advanced.author ? 1 : 0,
                "postTime": templateFormData.advanced.time ? 1 : 0,
                "postFeatured": templateFormData.themeSettings.postPopUp ? 1 : 0,
                "minimumPostWidth": 300,
                "padding": templateFormData.themeSettings.postPadding,
                "scrollDelay": 10,
                "slideDelay": 10,
                "numberOfPosts": templateFormData.themeSettings.numberOfPosts,
                "trimcontent": templateFormData.themeSettings.trimContent ? 1 : 0,
                "playFullVideo": 1,
                "randomizePost": templateFormData.themeSettings.suffleAssets ? 1 : 0,
                "hideUrls": 1,
                "fitRow": templateFormData.themeSettings.bestFit ? 1 : 0,
                "roundEdge": 5,
                "cardType": 1,
                "socialAction": 1,
                "timeForPopup": 10,
                "authorColor": templateFormData.cardStyle.authorColor,
                "iconType": 1,
                "iconColor": templateFormData.cardStyle.socialIconColor,
                "fontSize": parseInt(templateFormData.cardStyle.fontSize),
                "shareOption": 1,
                "hideContent": templateFormData.advanced.hideContent ? 1 : 0,
                "numberOfCoumn": templateFormData.themeSettings.columns && Object.keys(templateFormData.themeSettings.columns).length > 0 ? templateFormData.themeSettings.columns.value : 0,
                "slideDuration": 0,
                "slidePost": 0,
                "autoPlay": 0,
                "popupSlideShow": 0,
                "customUrl": "",
                "showHideOldPost": templateFormData.themeSettings.hideOldAssets.status ? 1 : 0,
                "showHideOlderPost": templateFormData.themeSettings.hideOldAssets.status ? 1 : 0,
                "loadMoreStatus": templateFormData.themeSettings.showmore ? 1 : 0,
                "autoScrollStatus": 0,
                "hidePosts": 0,
                "onsite_settings": "{}"
            }
            this.setState({ isRequesting: true, tempError: false })
            createTemplate(data).then((response) => {
                self.setState({ isRequesting: false, tempError: false })
                toast.success("Template has been successfully updated.")
                self.props.history("/template")
            }).catch((ex) => {
                toast.error("some error occurred")
            })
        }
        else {
            if (String(templateName).length == 0) this.setState({ tempError: true })
            toast.error("Please check all required fields.")
        }
    }




    render() {

        const { themeList, fonts, activeTheme } = this.props
        const { selectedTheme, selectedThemeData, templateName, templateFormData, currentTheme, isRequesting, tempError } = this.state
        return <>
            <FontHead fonts={fonts} />
            <div className="innerAside flex-shrink-0">
                <div className="card gutter-b">
                    <div className="card-body p-0">
                        <div className="innerAsideWrapper innerAsideWithAction p-3">
                            <div className="pb-3">
                                <label className="text-gray-800 mb-2">Template Name <strong className="text-danger">*</strong></label>
                                <div className="">
                                    <input type="text" name="templateName" className={`form-control form-control-sm ${tempError ? `is-invalid` : ``}`} placeholder="Enter Template name..." onChange={this.onTextTemplateName} value={templateName} />
                                    {tempError ? <div className="invalid-feedback">Please provide template name</div> : null}
                                </div>
                            </div>
                            <Accordion defaultActiveKey="1">
                                <Accordion.Item eventKey="1" className="pb-3">
                                    <Accordion.Header className="w-100">
                                        <span className="fs-9 fw-medium">{selectedTheme && selectedTheme.name ? selectedTheme.name : `Choose Theme`}</span>
                                    </Accordion.Header>
                                    <Accordion.Body className="border rounded-1 mt-1">
                                        <ToggleButtonGroup type="radio" className="flex-column w-100" name="themeName">
                                            {themeList.map((item, key) => {
                                                return <ToggleButton key={`theme_${item.themeId}`} id={`theme-${item.themeId}`} className={`p-0 card rounded-1 text-decoration-none cursor-pointer mb-2 ${item.themeId == selectedTheme.themeId ? `border-primary` : ``}`}
                                                    onClick={this.onChooseTheme({ name: item.name, themeId: item.themeId }, item)} value={item.themeId}>
                                                    <div className="card-body p-2">
                                                        <div className="d-flex align-items-center">
                                                            <div className="symbol symbol-40 flex-shrink-0 me-2 text-gray-400">
                                                                <ThemeView themeId={item.themeId} />
                                                            </div>
                                                            <div className="overflow-hidden">
                                                                <p className="fs-8 fw-semibold mb-1 text-gray-dark text-ellipsis text-start">{item.name}</p>
                                                                <p className="fs-9 fw-semibold mb-0 text-gray-600 text-ellipsis text-start">{item.product_type == 1 ? `Widget` : item.product_type == 2 ? `Display` : ``}</p>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </ToggleButton>
                                            })
                                            }
                                        </ToggleButtonGroup>
                                    </Accordion.Body>
                                </Accordion.Item>
                            </Accordion>
                            {
                                selectedTheme ? <>{selectedTheme.themeId == 4 ? <Suspense fallback={<></>}>
                                    <SquarePhotoTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} />
                                </Suspense> : selectedTheme.themeId == 21 ? <Suspense fallback={<></>}>
                                    <ModernSlideshowTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} />
                                </Suspense> : selectedTheme.themeId == 20 ? <Suspense fallback={<></>}>
                                    <ModernCardTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} />
                                </Suspense> : selectedTheme.themeId == 44 ? <Suspense fallback={<></>}>
                                    <AtmosphereTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} />
                                </Suspense> : selectedTheme.themeId == 50 ? <Suspense fallback={<></>}>
                                    <GalleryPhotoTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} />
                                </Suspense> : selectedTheme.themeId == 47 ? <Suspense fallback={<></>}>
                                    <HorizontalColumnTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} />
                                </Suspense> : selectedTheme.themeId == 58 ? <Suspense fallback={<></>}>
                                    <ModernWaterfallTemplate selectedThemeData={selectedThemeData} tempFormData={this.tempFormData} fonts={fonts} selectedTheme={selectedTheme} />
                                </Suspense> : null}</> : null
                            }
                        </div>
                        <div className="bg-white pt-2 w-100 p-3 rounded-2">
                            <button type="button" className={`btn btn-primary w-100 ${isRequesting ? `spinner` : ``}`} onClick={selectedTheme ? this.onClickSaveChanges : null}>Update Changes</button>
                        </div>
                    </div>
                </div>
            </div>
            <div className="d-flex flex-column overflow-hidden w-100">
                <div className="card">
                    <div className="card-body content_wrapper">
                        <div className="tb_app_container">
                            {templateFormData && Object.keys(templateFormData).length > 0 && selectedTheme && Object.keys(selectedTheme).length > 0 ? <div className="tb_app_wrapper">
                                <ThemesPreview themeName={selectedTheme.themeId} templateFormData={this.state.templateFormData} />
                            </div> : null}
                        </div>
                    </div>
                </div>
            </div>
        </>
    }
}

export default TemplateAside;